@import "../node_modules/reset-css/sass/reset";

:root {
  --darkblue: hsl(233, 26%, 24%);
  --limegreen: hsl(136, 65%, 51%);
  --brightcyan: hsl(192, 70%, 51%);
  --grayblue: hsl(233, 8%, 62%);
  --lightgrayblue: hsl(220, 16%, 96%);
  --lightgray: hsl(0, 0%, 98%);
  --gradient: linear-gradient(135deg, var(--limegreen), var(--brightcyan));
}
* {
  box-sizing: border-box;
}

body {
  font-family: "Public Sans", sans-serif;
  font-weight: 300;
  -webkit-text-size-adjust: 100%;
  font-size: 16px;
  background-color: var(--lightgrayblue);
}
h1,
h2,
h3 {
  font-weight: 300;
  color: var(--darkblue);
}
h1 {
  font-size: 2.4rem;
  line-height: 3rem;
  margin-bottom: 1rem;
}
h2 {
  font-size: 1.8rem;
  line-height: 2rem;
  margin-bottom: 1rem;
}
h3 {
  font-size: 1.2rem;
  line-height: 1.5rem;
  margin-bottom: 1rem;
}
p {
  font-weight: 400;
  color: var(--grayblue);
  line-height: 1.6rem;
  padding-bottom: 2rem;
}

.button {
  font-weight: 700;
  color: white;
  padding: 1rem 2rem;
  border-radius: 4rem;
  background: var(--gradient);
  text-decoration: none;
  margin-bottom: 2rem;
  cursor: pointer;
  transition: all 300ms ease-in-out;
  box-shadow: 0 0px 5px hsla(233, 26%, 24%, 0);
  &:hover {
    transform: translateY(-4px);
    box-shadow: 0 5px 15px hsla(233, 26%, 24%, 0.4);
  }
}

#navcontainer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
  perspective: 500px;
  perspective-origin: top;
  .branding {
    width: 100%;
    background-color: white;
    padding: 1.5rem;
    z-index: 160;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
  .nav__invite {
    display: none;
  }
  nav {
    transition: all 400ms ease-in-out;
    transform-origin: top;
    transform: rotate3d(1, 0, 0, -90deg);
    opacity: 0;
    width: 100%;
    margin: 2rem;
    padding: 1rem 0;
    background-color: var(--lightgray);
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 4px;
    filter: drop-shadow(0 10px 25px var(--darkblue));
    &.active {
      opacity: 1;
      transform: rotate3d(1, 0, 0, 0deg);
    }
    div {
      margin: 1rem 0;
      font-size: 1.2rem;
    }
  }
}

#hero {
  padding: 0 1rem 2rem 1rem;
  background-color: var(--lightgray);
  background-image: url(img/bg-intro-mobile.svg);
  background-repeat: no-repeat;
  background-position: top right;
  img {
    width: 100%;
    height: 364px;
    object-position: bottom;
    object-fit: cover;
  }
  .hero__text-container {
    padding: 0 1rem;
  }
  .hero__text {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
}

#features {
  flex-direction: column;
  padding: 4rem 2rem;
  display: grid;
  grid-template-rows: auto;
  text-align: center;
  img {
    padding-bottom: 1rem;
  }
}

#articles {
  background-color: var(--lightgray);
  padding: 4rem 2rem;
  .articles__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  h2 {
    margin-bottom: 2rem;
  }
}
article {
  background-color: white;
  border-radius: 4px;
  margin-bottom: 2rem;
  padding-bottom: 2rem;
  img {
    width: 100%;
    height: auto;
    border-radius: 4px 4px 0 0;
    margin-bottom: 1rem;
    display: block;
  }
  .author {
    display: block;
    font-weight: 700;
    font-size: 10px;
    color: var(--grayblue);
    margin-bottom: 1rem;
    padding: 0 1rem;
  }
  .title {
    font-size: 1.2rem;
    color: var(--darkblue);
    padding: 0 1rem;
    margin-bottom: 1rem;
  }
  p {
    padding: 0 1rem;
  }
}

footer {
  background-color: var(--darkblue);
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem 1rem;
  ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 2rem;
    li {
      color: var(--grayblue);
      padding: 0.5rem;
    }
  }
  .social {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 1rem 0;
    img {
      padding: 0.5rem;
    }
  }
}

@media (min-width: 767px) {
  #navcontainer {
    background-color: white;
    z-index: 200;
    .branding {
      max-width: 1200px;
      margin: 0 auto;
    }
    .button.nav__invite {
      display: block;
      margin-bottom: 0;
    }
    nav {
      width: 400px;
      margin: 0 auto;
      box-shadow: none;
      flex-direction: row;
      padding: 0;
      background-color: unset;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      z-index: 200;
      align-items: center;
      justify-content: center;
      height: 100%;
      opacity: 1;
      transform: unset;
      transition: unset;
      div {
        margin: 0 0.5rem;
        font-size: 1rem;
        cursor: pointer;
        position: relative;
        &:after,
        &:before {
          display: block;
          position: absolute;
          content: "";
          height: 1px;
          background-color: var(--grayblue);
          width: 100%;
          transition: transform 300ms ease-out;
          transform: scaleX(0);
          transform-origin: center;
        }
        &:before {
          top: -0.5rem;
        }
        &:after {
          bottom: -0.5rem;
        }

        &:hover {
          &:after,
          &:before {
            transform: scaleX(1);
          }
        }
      }
    }
    #open {
      display: none;
    }
  }
  #hero {
    padding: 4rem 2rem 0 2rem;
    height: 75vh;
    display: flex;
    background-position: 50vw top;
    background-image: url(img/bg-intro-desktop.svg);
    background-size: 120vh;
    position: relative;
    img {
      width: 40%;
      height: 118%;
      object-position: top left;
      order: 2;
      align-self: flex-end;
      position: absolute;
      top: 0;
      right: 0;
      max-width: 50%;
      max-height: 100vh;
    }
    .hero__text-container {
      order: 1;
      width: 100%;
      height: 100%;
      display: flex;
      max-width: 1200px;
      margin: 0 auto;
    }
    .hero__text {
      align-items: flex-start;
      text-align: left;
      max-width: 50%;
    }
  }
  .features__container {
    padding: 0 2rem;
  }
  #features {
    max-width: 1200px;
    margin: 0 auto;
    padding: 4rem 0;
    grid-template-rows: auto auto;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 2rem;
    text-align: left;
    .feature {
      grid-row: 2 / 3;
      &:nth-child(1) {
        grid-row: 1 / 2;
        grid-column: 1 /3;
      }
    }
  }
  #articles {
    .articles__container {
      flex-flow: row wrap;
      max-width: 1200px;
      align-items: stretch;
      margin: 0 auto;
    }
    img {
      display: block;
      max-height: 180px;
      width: 100%;
      object-fit: cover;
    }
    h2 {
      flex: 1 1 100%;
    }
    article {
      flex: 1 1 20%;
      margin-right: 2rem;
      &:last-child {
        margin-right: 0;
      }
    }
  }
}
